import { Component, ViewEncapsulation } from '@angular/core';

/* http://prismjs.com/download.html?themes=prism-okaidia&languages=markup+css+clike+javascript+typescript */

@Component({
  selector: 'app-prism-css-wrapper',
  encapsulation: ViewEncapsulation.None,
  template: `
  <div class="prism-css-wrapper">
    <ng-content></ng-content>
  </div>`,
  styles: [`
    code[class*="language-"],
    pre[class*="language-"] {
      color: #f8f8f2;
      background: none;
      text-shadow: 0 1px rgba(0, 0, 0, 0.3);
      font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
      text-align: left;
      white-space: pre;
      word-spacing: normal;
      word-break: normal;
      word-wrap: normal;
      line-height: 1.5;
    
      -moz-tab-size: 4;
      tab-size: 4;
    
      -webkit-hyphens: none;
      -moz-hyphens: none;
      -ms-hyphens: none;
      hyphens: none;
    }
    
    /* Code blocks */
    pre[class*="language-"] {
      padding: 1em;
      margin: .5em 0;
      overflow: auto;
      border-radius: 0.3em;
    }
    
    :not(pre) > code[class*="language-"],
    pre[class*="language-"] {
      background: #272822;
    }
    
    /* Inline code */
    :not(pre) > code[class*="language-"] {
      padding: .1em;
      border-radius: .3em;
      white-space: normal;
    }
    
    .token.comment,
    .token.prolog,
    .token.doctype,
    .token.cdata {
      color: slategray;
    }
    
    .token.punctuation {
      color: #f8f8f2;
    }
    
    .namespace {
      opacity: .7;
    }
    
    .token.property,
    .token.tag,
    .token.constant,
    .token.symbol,
    .token.deleted {
      color: #C076F3;
    }
    
    .token.boolean,
    .token.number {
      color: #ae81ff;
    }
    
    .token.selector,
    .token.attr-name,
    .token.string,
    .token.char,
    .token.builtin,
    .token.inserted {
      color: #a6e22e;
    }
    
    .token.operator,
    .token.entity,
    .token.url,
    .language-css .token.string,
    .style .token.string,
    .token.variable {
      color: #f8f8f2;
    }
    
    .token.atrule,
    .token.attr-value,
    .token.function {
      color: #e6db74;
    }
    
    .token.keyword {
      color: #66d9ef;
    }
    
    .token.regex,
    .token.important {
      color: #fd971f;
    }
    
    .token.important,
    .token.bold {
      font-weight: bold;
    }
    .token.italic {
      font-style: italic;
    }
    
    .token.entity {
      cursor: help;
    }
  `],
})
export class PrismCssWrapperComponent {

}



